.mypage-container {
    max-width: 800px; /* 최대 너비를 800px로 확장 */
    width: 90%; /* 기본 너비를 90%로 설정하여 반응형 디자인 */
    min-width: 300px; /* 최소 너비 설정 */
    margin: 20px auto; /* 상하 20px의 마진과 중앙 정렬 */
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    text-align: left; /* 텍스트를 왼쪽 정렬 */
}

.user-info {
    margin-bottom: 20px;
}

.user-info p {
    font-size: 16px;
    margin: 10px 0;
}

.edit-button, .save-button, .cancel-button, .logout-button {
    padding: 10px 15px;
    margin: 10px 5px;
    color: #fff;
    border: none;
    cursor: pointer;
    border-radius: 5px;
}

.edit-button:hover, .save-button:hover, .cancel-button:hover, .logout-button:hover {
    background-color: rgb(132, 168, 223);
}

.edit-input {
    padding: 5px;
    margin-left: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
}
