.notice {
    margin-top: 0px; /* 상단 마진 제거 */
    width: 100%; /* notice 컴포넌트를 전체 너비로 확장 */
}

.notice-container {
    width: 100%;
    margin: 0 auto; /* 중앙 정렬을 위해 상하 마진을 0으로 설정 */
    overflow: hidden;
}

.notice-content {
    padding: 20px;
    color: white;
    text-align: left;
    width: 100%;
    height: 100%;
    margin-left: 0%; /* 좌우 정렬을 균형 있게 설정 */
}

.notice-slide {
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    background-position: center;
    position: relative;
    width: 100%; /* 슬라이드가 전체 너비를 차지하도록 설정 */
    height: 400px; /* 슬라이드 높이를 설정, 필요시 조정 가능 */
    background-repeat: no-repeat; /* 이미지 반복 방지 */
    border-radius: 10px;
}



.notice-label {
    background-color: rgba(156, 179, 214, 0.76);
    color: white;
    padding: 5px 10px;
    border-radius: 5px;
    font-weight: bold;
    margin-bottom: 50px;
    display: inline-block;
}

.notice-slide h2 {
    margin: 0;
    font-size: 24px;
}

.notice-slide p {
    margin-top: 10px;
    font-size: 16px;
}

/* 인디케이터 스타일 조정 */
.carousel .control-dots {
    position: absolute;
    bottom: 0px; /* 인디케이터를 컨테이너 안으로 위치시키기 위해 설정 */
    width: 100%;
    text-align: center;
}

.carousel .control-dots .dot {
    background: white; /* 인디케이터의 색상 */
    box-shadow: none; /* 그림자 제거 */
    width: 10px;
    height: 10px;
    margin: 0 5px;
    border-radius: 50%;
    display: inline-block;
}