/* 차트 스타일링 */

@font-face {
    font-family: 'Godo';
    font-style: normal;
    font-weight: 400;
    src: url('//fastly.jsdelivr.net/korean-webfonts/1/corps/godo/Godo/GodoM.woff2') format('woff2'), url('//fastly.jsdelivr.net/korean-webfonts/1/corps/godo/Godo/GodoM.woff') format('woff');
}

@font-face {
    font-family: 'Godo';
    font-style: normal;
    font-weight: 700;
    src: url('//fastly.jsdelivr.net/korean-webfonts/1/corps/godo/Godo/GodoB.woff2') format('woff2'), url('//fastly.jsdelivr.net/korean-webfonts/1/corps/godo/Godo/GodoB.woff') format('woff');
}

.godo * {
    font-family: 'Godo', sans-serif;
}

.chart {
    display: flex;
    flex-direction: column;
    justify-content: center;  /* 수직 가운데 정렬 */
    align-items: center;      /* 수평 가운데 정렬 */
    height: 500px;            /* 화면 전체를 채우도록 높이 설정 */
    width: 100%;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.charts {
    width: 100%;         /* 차트가 가로로 전체 화면을 차지하도록 설정 */
    max-width: 1200px;    /* 최대 가로 크기를 1200px로 설정 */
    margin: 10px;
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    box-sizing: border-box;
    height: 600px;        /* 차트의 높이를 600px로 설정 */
}

.title-section {
    font-family: 'Godo', sans-serif;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
    margin-bottom: 20px;
}

.section-title {
    font-size: 24px;
    font-weight: bold;
    margin: 0;
}

@media (max-width: 768px) {
    .charts {
        height: 500px;    /* 작은 화면에서는 높이를 500px로 조정 */
    }
}

@media (max-width: 480px) {
    .charts {
        height: 400px;    /* 모바일에서는 높이를 400px로 조정 */
    }
}