@font-face {
    font-family: 'ONE-Mobile-POP';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/ONE-Mobile-POP.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    scroll-behavior: smooth; /* 스크롤을 부드럽게 */
    overflow: hidden; /* 스크롤바 숨기기 (선택 사항) */
}

.shelter-container {
    scroll-snap-type: y mandatory; /* y축 스크롤 스냅 활성화 */
    overflow-y: scroll; /* 세로 스크롤 활성화 */
    height: 100vh; /* 전체 화면 크기 */
    scroll-behavior: smooth; /* 스크롤을 부드럽게 */
}

.shelter {
    position: relative;
    scroll-snap-align: start;
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    opacity: 0;
    transform: translateY(50px);
    transition: opacity 0.8s ease-out, transform 0.8s ease-out;
    
}

.shelter.visible {
    opacity: 1;
    transform: translateY(0); /* 보일 때 위에서 내려옴 */
}

.shelter h1, .shelter p {
    font-family: 'ONE-Mobile-POP';
    text-align: center;
    position: relative;
    z-index: 10; /* 텍스트가 맨 앞에 오도록 설정 */
    margin-bottom: 20px;
    font-size: 3rem;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}

.shelter p {
    text-align: center;
    max-width: 800px;
    margin: 20px auto;
    font-size: 2rem;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}

/* 마우스 위치에 따라 나타나는 원형 마스크 */
.mask {
    position: absolute;
    width: 300px;
    height: 300px;
    border-radius: 50%;
    background-color: transparent;
    box-shadow: 0 0 0 200vw rgba(239, 242, 245, 0.76); /* 반투명한 원 */
    pointer-events: none; /* 마우스 클릭 방지 */
    z-index: 3;
}
