@font-face {
    font-family: 'Godo';
    font-style: normal;
    font-weight: 400;
    src: url('//fastly.jsdelivr.net/korean-webfonts/1/corps/godo/Godo/GodoM.woff2') format('woff2'), url('//fastly.jsdelivr.net/korean-webfonts/1/corps/godo/Godo/GodoM.woff') format('woff');
}

@font-face {
    font-family: 'Godo';
    font-style: normal;
    font-weight: 700;
    src: url('//fastly.jsdelivr.net/korean-webfonts/1/corps/godo/Godo/GodoB.woff2') format('woff2'), url('//fastly.jsdelivr.net/korean-webfonts/1/corps/godo/Godo/GodoB.woff') format('woff');
}

.godo * {
    font-family: 'Godo', sans-serif;
}

h2 {
    font-family: 'Godo', sans-serif;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 10px;
}

.store {
    text-align: center;
}

.category-list-horizontal {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #ddd;
    padding: 10px 0;
    background-color: #fff;
}

.category-list-horizontal button {
    padding: 10px 15px;
    background: none;
    border: none;
    font-size: 16px;
    cursor: pointer;
    margin: 0 10px;
    /* 버튼 사이 간격 */
    transition: color 0.3s ease;
}

.category-list-horizontal button:hover {
    color: #007BFF;
    /* hover 시 색상 변경 */
}

.category-list-horizontal .active {
    font-weight: bold;
    color: #000;
    /* 현재 활성화된 버튼 스타일 */
}

.product-list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
}

.product-card {
    background-color: #fff;
    border: 1px solid #ddd;
    padding: 15px;
    text-align: center;
    height: 350px; /* 모든 카드의 고정 높이 설정 */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.product-card h2 {
    font-size: 16px;
    margin: 10px 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}


.product-image {
    width: 100%;
    height: 200px; /* 이미지의 고정 높이 설정 */
    object-fit: cover; /* 이미지를 카드에 맞게 자르기 */
}


.pagination {
    display: flex;
    justify-content: center;
    margin: 20px 0;
}

.pagination button {
    background-color: #f1f1f1;
    border: 1px solid #ddd;
    padding: 10px 15px;
    margin: 0 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.pagination button.active {
    background-color: rgb(132, 168, 223);
    color: white;
}

.pagination button:hover {
    background-color: rgb(132, 168, 223);
    color: white;
}

.pagination button[disabled] {
    background-color: #ccc;
    color: black;
    cursor: not-allowed;
  }
  
  /* 화살표 버튼 스타일 */
  .pagination button:first-of-type,
  .pagination button:last-of-type {
    font-weight: bold;
    font-size: 18px;
  }
