/* 기본적인 요소들을 리셋하여 브라우저 호환성을 높입니다 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
}

/* 메인 컨테이너 */
.App {
  margin-left: 200px;
  margin-top: 60px; /* 헤더 높이만큼 위로 이동 */
  display: flex;
  flex-direction: column;
  height: 100vh;
}

/* 헤더 스타일링 */
header {
  flex-shrink: 0;
}

/* 메인 콘텐츠 영역 */
.AppContent {

  
  padding: 20px;
  flex-grow: 1;
  overflow-y: auto;
  background-color: #f8f9fa;
}



/* 반응형 디자인을 위한 미디어 쿼리 */
@media (max-width: 768px) {
  .adoft-chart-container {
    flex-direction: column;
  }
}

/* 카드 스타일 개선 */
.animal-card {
    flex: 1 1 21%;
    margin: 10px;
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
    background: linear-gradient(145deg, #f0f0f0, #ffffff);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.animal-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
}

/* 이미지 스타일링 */
.animal-image {
    width: 100%;
    border-radius: 10px;
    filter: brightness(0.95);
}

/* 폰트 및 텍스트 스타일 */
.animal-card h2 {
    font-family: 'Roboto', sans-serif;
    font-size: 1.5em;
    margin-bottom: 10px;
}

.animal-card p {
    font-family: 'Roboto', sans-serif;
    color: #666;
    margin-bottom: 8px;
}

/* 반응형 디자인 */
@media (max-width: 768px) {
    .animal-card {
        flex: 1 1 48%;
    }
}

@media (max-width: 480px) {
    .animal-card {
        flex: 1 1 100%;
        margin: 10px 0;
    }
}

