@font-face {
  font-family: 'Godo';
  font-style: normal;
  font-weight: 400;
  src: url('//fastly.jsdelivr.net/korean-webfonts/1/corps/godo/Godo/GodoM.woff2') format('woff2'), url('//fastly.jsdelivr.net/korean-webfonts/1/corps/godo/Godo/GodoM.woff') format('woff');
}

@font-face {
  font-family: 'Godo';
  font-style: normal;
  font-weight: 700;
  src: url('//fastly.jsdelivr.net/korean-webfonts/1/corps/godo/Godo/GodoB.woff2') format('woff2'), url('//fastly.jsdelivr.net/korean-webfonts/1/corps/godo/Godo/GodoB.woff') format('woff');
}

.godo * {
  font-family: 'Godo', sans-serif;
}

.adoft{
  font-family: 'Godo', sans-serif;
}

.adoft-dog{
  text-align: left;
}

.animal-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: stretch;
    gap: 10px; /* 카드 사이의 간격을 조정 */
}

.animal-card {
    flex: 1 1 calc(25% - 20px); /* 한 줄에 카드 4개를 표시하려면 25% 설정, 상황에 맞게 비율을 조정하세요 */
    margin: 10px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    background-color: white;
}


.view-all {
  margin-top: 20px;
}

.view-all a {
  text-decoration: none;
  color: #007bff;
  font-weight: bold;
}

.container {
  display: flex;
  justify-content: space-between;
  /* 카드들 사이의 간격을 균등하게 배분 */
  align-items: stretch;
  /* 자식 요소들을 동일한 높이로 맞춤 */
  padding: 20px;
  gap: 20px;
  /* 컴포넌트 사이의 간격 */
}

@media (max-width: 768px) {
  .chart-and-adoft {
    flex-direction: column;
  }

  .chart-container,
  .adoft-container {
    width: 100%;
    /* 모바일에서 전체 너비 */
    margin-bottom: 20px;
    /* 컴포넌트 사이 간격 추가 */
  }

  .animal-card {
    flex: 1 1 100%;
  }
}

@media (max-width: 480px) {
  .animal-card {
    flex: 1 1 100%;
    margin: 10px 0;
    /* 카드 사이 간격 조정 */
  }
}

/* Adoft.css */

.adoft {
  margin: 10px;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* 수직 중앙 정렬 */
  text-align: center;
  padding: 20px;
  box-sizing: border-box;
  height: auto;
}



/* 동물 카드 리스트 스타일링 */
.animal-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 20px;
  /* 카드 사이의 간격 설정 */
}

/* 동물 카드 스타일링 */
.animal-card {
  flex: 1 1 calc(20% - 20px);
  border-radius: 10px;
  padding: 20px;
  margin-bottom: 20px;
  /* 아래쪽 간격 추가 */
  text-align: left;
  box-sizing: border-box;
  /* 패딩과 보더를 포함한 너비 계산 */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* 카드 내부 요소 간격 균등 배분 */
  min-height: 458px;
  /* 최소 높이 설정 */
}

/* 동물 이미지 스타일링 */
.animal-image {
  width: 100%;
  height: 300px;
  object-fit: cover;
  border-radius: 10px;
  display: block;
}

.view-all {
  margin-top: 20px;
  display: flex;
  justify-content: center; /* 버튼을 중앙에 배치 */
}

.view-all a {
  text-decoration: none;
  color: #007bff;
  font-weight: bold;
}

.pagination {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.pagination button {
  background-color: #f1f1f1;
  border: 1px solid #ddd;
  padding: 10px 15px;
  margin: 0 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.pagination button.active {
  background-color: rgb(132, 168, 223);
  color: white;
}

.pagination button:hover {
  background-color: #ddd;
}

.pagination button[disabled] {
  background-color: #ccc;
  color: black;
  cursor: not-allowed;
}

/* 화살표 버튼 스타일 */
.pagination button:first-of-type,
.pagination button:last-of-type {
  font-weight: bold;
  font-size: 18px;
}
