/* AdoptionProcedure.css */

body {
    background-color: #f4f4f9;
    font-family: 'Arial', sans-serif;
}

.adoption {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px;
    padding: 20px;
    border-radius: 8px;
}

.preparation {
    width: 100%;
    background-color: #F5E9DC;
    padding: 20px;
    border-radius: 8px;
    margin-bottom: 20px;
}

.preparation h2 {
    background-color: #D1A055;
    color: white;
    padding: 10px 20px;
    border-radius: 25px;
    text-align: center;
    margin: 0 auto 20px auto;
    display: inline-block;
}

.section-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 100%;
}

.section {
    flex: 1 1 30%; /* 각 섹션의 너비를 30%로 설정 */
    margin: 10px;
    padding: 20px;
    border-radius: 8px;
    background: linear-gradient(to bottom, #ffffff, #f9f9f9);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border: 1px solid #ddd;
    transition: transform 0.3s;
    min-width: 300px;
}

.procedure {
    border: 2px solid rgb(156, 179, 214);
}

.conditions {
    border: 2px solid rgb(156, 179, 214);
}

.section h1 {
    font-size: 24px;
    margin-bottom: 20px;
    color: #333;
    text-align: center;
    position: relative;
    padding-bottom: 10px;
    border-bottom: 2px solid #ddd;
}

.section ol, .section ul {
    list-style: none;
    padding: 0;
}

.section ol li, .section ul li {
    margin-bottom: 15px;
    padding-left: 25px;
    position: relative;
}

.section ol li h2, .section ul li {
    font-size: 20px;
    margin-bottom: 5px;
    color: #555;
}

.section ol li::before, .section ul li::before {
    content: '\2022';
    font-size: 24px;
    position: absolute;
    left: 0;
    top: 0;
}



.section p {
    font-size: 16px;
    color: #666;
}

.preparation .content {
    display: flex;
    align-items: center;
}

.preparation .prep-image {
    width: 200px;
    height: auto;
    margin-right: 20px;
}

.preparation ul {
    list-style-type: none;
    padding: 0;
}

.preparation ul li {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.preparation ul li::before {
    content: '✔️';
    margin-right: 10px;
    color: #D1A055;
}

@media (max-width: 768px) {
    .section-wrapper {
        flex-direction: column;
        align-items: center;
    }
    
    .section {
        flex: 1 1 100%;
        margin: 10px 0;
    }
}
